<template>
  <div>
    <div class="grid-container" @dragover.prevent="onDragOver" @drop.prevent="onDrop">
      <div v-for="(image, index) in images" :key="index" class="grid-item">
        <img :src="image" :alt="`Image ${index}`" @click="selectImage(image)">
      </div>
    </div>
    <button @click="uploadImages">Upload Images {{ progress }}</button>
    <ImageWithPredictions v-if="selectedImage"
      :image="selectedImage"
      :predictions="predictions"
    />
  </div>

</template>
  
<script>
import api from '../services/mockapi.js'
import ImageWithPredictions from './ImageWithPredictions.vue'
export default {
  name: 'ImageGrid',
  components: {
    ImageWithPredictions
  },
  data() {
    return {
      images: [],
      progress: "",
      selectedImage: null,
      predictions: [
        {x: 40, y: 40, width: 120, height: 80, label: "dog"}
      ]
    };
  },
  methods: {
    onDragOver() {
      // Necessary to allow the drop event to fire
    },
    onDrop(event) {
      // Handle dropped image files
      const files = event.dataTransfer.files;
      Array.from(files).forEach((file) => {
        if (file.type.startsWith('image/')) {
          const reader = new FileReader();
          reader.onload = (e) => {
            this.images.push(e.target.result);
          };
          reader.readAsDataURL(file);
        }
      });
    },
    uploadImages() {
      this.progress = "0%";
      let uploadCount = 0;
      this.images.forEach((image) => {
        api.uploadImage(image).then(() => {
          uploadCount++;
          if (uploadCount === this.images.length) {
            this.progress = "";
          } else {
            this.progress = `${parseInt(100 * uploadCount / this.images.length)}%`;
          }
        })
      });
    },
    selectImage(image){
      this.selectedImage = image;
    }
  }
};
</script>
  
  <style scoped>
  .grid-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    gap: 10px;
    background-color: blue;
    width: 100%;
    height: 100px;
    /* Add other styles like padding, border, etc. */
  }
  
  .grid-item img {
    width: 100%;
    height: auto;
    /* Add other styles to fit your design */
  }
  </style>
  