<template>
  <div>
    <h2 class="text-4xl">Hello world {{ counter }}</h2>
    <button @click="incr()">increment</button>
    <button @click="toggle()">toggle</button>
    <div v-if="showRed" style="width: 100px; height: 100px; background-color: red"></div>
  </div>
</template>
<script>
export default {
  name: "Hello",
  data() {
    return {
      counter: 0,
      showRed: true
    };
  },
  methods: {
    incr() {
      this.counter += 1;
    },
    toggle() {
      this.showRed = !this.showRed;
    },
  }
};
</script>
  